.listing__thumbnail {
  object-fit: contain;
  height: 200px;
  width: 200px;
  border-radius: 4px;
  background-color: black;
}

.listing {
  display: block;
  margin-left: 5px;
  margin-right: 5px;
  padding: 10px;
}

.listing__username {
  margin-left: 0;
  font-weight: 600;
}

.listing__title {
  font-weight: 600;
}

.header__left {
  object-fit: contain;
  height: 40px;
  width: 40px;
  border-radius: 300px;
  margin-right: 5px;
  margin-top: 2px;
}

.listing__header {
  display: flex;
}

.listing__like {
  display: block;
  margin-right: 150px;
}

.listing__more {
  display: block;
}

.listing__footer {
  width: 100%;
  margin-top: 30px;
}

.listing__time {
  color: darkgray;
  font-weight: 600;
  font-size: small;
}

.listing:hover {
  box-shadow: 0 0 7px lightgray;
}
